<template>
  <div class="ao-asset-money">
    <ao-asset-return></ao-asset-return>
    <ao-asset-currency :title="title" :lines="lines" :unit="unit"></ao-asset-currency>
    <div class="content">
      <div class="form">
        <div class="form-item">
          <div class="title">
            <span>{{ money_text[0] }}</span>
          </div>
          <!-- <input v-model="address" class="input-box" type="text" /> -->
          <el-select class="input-box" v-model="address" :placeholder="money_text[1]">
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.address"
              :value="item.address"
            ></el-option>
          </el-select>
        </div>
        <div class="form-item">
          <div class="title">
            <span>{{ money_text[2] }}</span>
            <span>
              {{ money_text[3] }}
              <span class="value">{{ money }}</span>
              {{ item.currency }}
            </span>
          </div>
          <input v-model="number" class="input-box" type="number" v-on:input="input" />
        </div>
        <div class="form-item form-item-col">
          <div class="col">
            <div class="title">
              <span>{{ money_text[4] }}{{ rate }}%</span>
            </div>
            <div class="input-box">
              <span>{{ rateMoney }}</span>
              <span>{{ item.currency }}</span>
            </div>
          </div>
          <div class="col">
            <div class="title">
              <span>{{ money_text[5] }}</span>
            </div>
            <div class="input-box">
              <span>{{ getMoney }}</span>
              <span>{{ item.currency }}</span>
            </div>
          </div>
        </div>
        <div class="form-item">
          <div class="title">
            <span>{{ money_text[6] }}</span>
          </div>
          <input v-model="password" class="input-box" type="password" />
        </div>
        <div class="form-item">
          <el-button type="primary" @click="take">{{ money_btn }}</el-button>
        </div>
      </div>
      <div class="text">{{ money_msg[0] }}</div>
      <ul>
        <li class="text">{{ money_msg[1] }}</li>
        <li class="text">{{ money_msg[2] }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import AoAssetReturn from "./components/AoAssetReturn";
import AoAssetCurrency from "./components/AoAssetCurrency";
import { getWalletLine, tokeMoney, getAddressList } from "@/api/asset";

export default {
  components: {
    AoAssetReturn,
    AoAssetCurrency
  },
  data() {
    return {
      money_text: this.$t('asset.money_text'),
      money_btn: this.$t('asset.money_btn'),
      money_msg: this.$t('asset.money_msg'),

      options: [],
      title: "",
      lines: [],
      money: "0.00000000",
      unit: "",
      rate: "",
      item: {},
      rateMoney: "",
      getMoney: "",
      address: "",
      number: "",
      password: "",
      wid: "",
      type: ""
    };
  },
  mounted() {
    var __data = JSON.parse(localStorage.getItem("jyxt_asset_table_path")); //this.$store.state.__data;
    console.log(__data);
    if (!__data) {
      this.$router.back(-1);
    }
    this.unit = __data.unit;
    this.title = this.$t('asset.zh_title') + __data.unit + " " + __data.money;
    this.wid = __data.id;
    this.type = __data.type;
    if (__data.currency == "USDT") {
      this.item = __data;
      this.lines = [__data];
      this.money = __data.money;

    }
    getWalletLine({ wid: this.wid }).then(res => {
      if (res.item) {
        this.item = res.item;
        this.lines = [res.item];
      }
      this.money = res.money
      this.rate = res.rate;
      getAddressList({
        id: this.type,
        page: 1,
        currency: this.item.currency
      }).then(res => {
        this.options = res;
      });
    });
  },
  methods: {
    ffff(v, l = 8) {
      return parseFloat(v).toFixed(l);
    },
    input() {
      this.rateMoney = this.ffff((this.number * this.rate) / 100);
      this.getMoney = this.ffff(this.number - this.rateMoney);
    },
    take() {
      var data = {
        address: this.address,
        number: this.number,
        password: this.password,
        wid: this.wid,
        type: this.type
      };
      tokeMoney(data)
        .then(res => {
          this.$router.back(-1);
        })
        .catch(res => {});
    }
  }
};
</script>
<style scoped lang="scss">
.ao-asset-money {
  .content {
    width: 100%;
    padding: 0 30px;
    color: #c7cce6;
  }
  .form {
    .form-item-col {
      display: flex;
      .col {
        flex: 1;
        &:last-child {
          margin-left: 20px;
          .input-box {
            background: #1e2c42;
          }
        }
      }
    }
    .title {
      display: flex;
      justify-content: space-between;
    }
    span {
      color: #637085;
      margin-bottom: 15px;
      font-size: 12px;
      &.value {
        color: #5697f4;
      }
    }
    .input-box {
      width: 100%;
      border: 1px solid #6b80ae;
      border-radius: 3px;
      padding: 15px;
      color: #c7cce6;
      margin-bottom: 30px;
      background: none;
      font-size: 12px;
      .el-input__inner {
        background: transparent;
        border: none;
        height: 100%;
      }
      span {
        margin-bottom: 0;
        color: #c7cce6;
      }
    }
    div.input-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    width: 100%;
    .el-button {
      flex: 1;
      width: 200px;
      line-height: 46px;
      background-color: #7a98f7;
      border-radius: 4px;
      color: #fff;
      border: none;
      padding: 0;
      float: right;
    }
  }
  .text {
    color: #637085;
    font-size: 12px;
  }
  ul {
    margin-top: 15px;
  }
  ul li {
    list-style: disc inside;
  }
}
</style>

<style lang="scss">
.ao-asset-money .content .form .input-box {
  padding-left: 0;
  padding-right: 0;
}
.el-input__inner {
  background: transparent;
  border: none;
  padding: 0 15px;
  height: auto;
  line-height: normal;
  color: #c7cce6;
}
::-webkit-scrollbar {
  background: transparent;
}
</style>



