<template>
  <div class="ao-asset-currency">
    <div class="table table-2">
      <div class="theader">
        <div class="th th-1">
          <div class="td left">{{ title }}</div>
          <div class="td center"></div>
          <div class="td right"></div>
        </div>
        <div class="th th-2">
          <div class="td left">{{ td[0] }}</div>
          <div class="td center">{{ td[1] }}</div>
          <div class="td center">{{ td[2] }}</div>
          <div class="td right">{{ td[3] }}({{ unit }})</div>
        </div>
      </div>
      <div class="tbody">
        <div class="tr" v-for="(item, index) in lines" :key="index">
          <div class="td left">{{ item.currency }}</div>
          <div class="td center">{{ ffff(item.positions) }}</div>
          <div class="td center">{{ ffff(item.positions_lock) }}</div>
          <div class="td right">{{ ffff(item.zh)  }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AoAssetCurrency",
  props: {
    title: {
      type: String,
      default: ''
    },
    unit: {
      type: String,
      default: ''
    },
    lines: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      td: this.$t('asset.currency_td')
    };
  },
  methods: {
    ffff(v=0, l=4) {
      return parseFloat(v).toFixed(l)
    }
  }
};
</script>

<style scoped lang="scss">
.ao-asset-currency {
  .table {
    margin: 10px;
    padding: 0 20px;
    background: #171c2b;
    .theader,
    .tbody {
      padding: 0 20px;
    }
    .th,
    .tr {
      display: flex;
    }
    .th {
      line-height: 1.5;
    }
    .th .td,
    .tr .td {
      color: #728daf;
      font-size: 12px;
      flex: 1;
    }
    .th .td,
    .tr .td {
      &.left {
        text-align: left;
      }
      &.center {
        text-align: center;
      }
      &.right {
        text-align: right;
      }
    }
    .th .td {
      font-size: 14px;
      color: #fff;
      line-height: 3;
    }
    .tr {
      padding: 15px 0;
      &.border {
        border-top: 1px solid #1e2c42;
      }
    }
    .more {
      color: #c7cce6;
      font-size: 12px;
      width: 100%;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
    }
  }
  .table.table-2 {
    .th-1 .td {
      font-size: 16px;
    }
    .th-2 .td {
      color: #637085;
      font-size: 12px;
    }
    .tr {
      border: none;
    }
    .tr .td {
      color: #fff;
      font-size: 18px;
    }
  }
}
</style>



